.resumePage__sub {
    display: flex;
    justify-content: space-between;
  }
  
  .resumePage__sub > div {
    flex-basis: calc(50% - 32px);
  }
  
  .resumeSub__title {
    margin-top: 0;
  }
  
  .timelines {
    border-left: 2px solid #dadada;
    padding: 8px 0 8px 48px;
  }
  
  .timelines > div {
    margin-top: 16px;
    margin-bottom: 16px;
  }
  
  .timeline {
    position: relative;
    padding: 16px 20px;
    border-radius: 4px;
    box-shadow: 0px 3px 8px 0px rgba(0,0,0,0.1);
    border-left: 2px solid #ffb703;
    margin-left: -5px;
  }
  
  .timeline::before {
    content: "";
    width: 48px;
    border-bottom: 2px solid #ffb703;
    position: absolute;
    left: -48px;
    top: 8px;
  }
  
  .timeline::after {
    content: "";
    position: absolute;
    left: -52px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    border: 2px solid #ffb703;
    top: 2px;
    background: white;
  }
  
  .experience__title {
    margin: 0 0 8px;
  }
  
  .experience__meta {
    font-size: 0.8rem;
    margin-bottom: 8px;
  }
  
  .experience__year {
    color: #ffb703;
  }
  
  .experience__divider {
    border-right: 1px solid #dadada;
  }
  
  .experience__address {
    color: #49515d;
  }
  
  .experience__description {
    line-height: 1.75;
  }
  
  .resumePage__sub + .resumePage__sub {
    margin-top: 32px;
  }
  
  .resumeSub__titleHighlight {
    color: #ffb703;
  }
  
  .skill {
    margin: 16px 0;
  }
  
  .skill__title {
    margin: 0 0 8px;
    font-size: 0.8rem;
  }
  
  .skill__level {
    position: relative;
    height: 16px;
    background-color: rgba(0,0,0,0.1);
  }
  
  .skill__level:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #ffb703;
  }
  
  .skill__level--apiDesign:after {
    width: 80%;
  }
  
  .skill__level--webDesign:after {
    width: 70%;
  }
  
  .skill__level--databaseDesign:after {
    width: 75%;
  }
  
  .skill__level--htmlDesign:after {
    width: 90%;
  }
  
  .skill__level--cssDesign:after {
    width: 85%;
  }
  
  .skill__level--jsDesign:after {
    width: 70%;
  }